// mcp/client.ts
function createMcpClient(opts) {
  const fetchFn = opts.fetchImpl ?? globalThis.fetch;
  const baseUrl = opts.baseUrl.replace(/\/$/, "");
  const defaultHeaders = opts.headers ?? {};
  let nextId = 1;
  let sessionId = null;
  function getMcpEndpoint() {
    const normalized = baseUrl.replace(/\/$/, "");
    return normalized.endsWith("/mcp") ? normalized : `${normalized}/mcp`;
  }
  async function ensureSession() {
    if (sessionId) return sessionId;
    const req = {
      jsonrpc: "2.0",
      id: nextId++,
      method: "initialize",
      params: {
        protocolVersion: "2025-03-26",
        capabilities: {},
        clientInfo: { name: "@sapience/sdk", version: "0.1.0" }
      }
    };
    const res = await fetchFn(`${getMcpEndpoint()}`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json, text/event-stream",
        ...defaultHeaders
      },
      body: JSON.stringify(req)
    });
    if (!res.ok) {
      const text = await res.text().catch(() => "");
      throw new Error(`MCP initialize failed (${res.status}): ${text}`);
    }
    const sid = res.headers.get("mcp-session-id");
    if (!sid) throw new Error("MCP initialize missing mcp-session-id");
    sessionId = sid;
    return sid;
  }
  return {
    async callTool(name, args) {
      await ensureSession();
      const req = {
        jsonrpc: "2.0",
        id: nextId++,
        method: "tools/call",
        params: { name, arguments: args || {} }
      };
      const res = await fetchFn(`${getMcpEndpoint()}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json, text/event-stream",
          "mcp-session-id": sessionId,
          ...defaultHeaders
        },
        body: JSON.stringify(req)
      });
      const json = await res.json().catch(() => ({}));
      if (!res.ok || json.error) {
        const message = json.error?.message || `HTTP ${res.status}`;
        throw new Error(`MCP tool error: ${message}`);
      }
      return json.result ?? void 0;
    },
    async readResource(uri) {
      await ensureSession();
      const req = {
        jsonrpc: "2.0",
        id: nextId++,
        method: "resources/read",
        params: { uri }
      };
      const res = await fetchFn(`${getMcpEndpoint()}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json, text/event-stream",
          "mcp-session-id": sessionId,
          ...defaultHeaders
        },
        body: JSON.stringify(req)
      });
      const json = await res.json().catch(() => ({}));
      if (!res.ok || json.error) {
        const message = json.error?.message || `HTTP ${res.status}`;
        throw new Error(`MCP resource error: ${message}`);
      }
      return json.result ?? void 0;
    },
    async close() {
      try {
        const sid = sessionId;
        if (!sid) return;
        await fetchFn(`${getMcpEndpoint()}`, {
          method: "DELETE",
          headers: { "mcp-session-id": sid, ...defaultHeaders }
        }).catch(() => {
        });
      } finally {
        sessionId = null;
      }
    }
  };
}

export {
  createMcpClient
};
